<div class="main-content" v-if="getFullWeather"><div class="main-content-left"><span class="main-content-left__degree">{{ Math.round(getFullWeather.current.temp) }}°</span><span class="main-content-left__today">Сегодня</span><span class="main-content-left__time">Время: {{getHour}}:{{getMinute}}</span><span class="main-content-left__city">Город: {{ getFullWeather.city }}</span><img src="@/assets/img/sun.svg" alt="alt" v-if="getMain == 'Clear'"><img src="@/assets/img/mainly_cloudy.svg" alt="alt" v-else-if="getMain == 'Clouds'"><img src="@/assets/img/rain.svg" alt="alt" v-else-if="getMain == 'Rain'"><img src="@/assets/img/snow.svg" alt="alt" v-else-if="getMain == 'Snow'"></div><div class="main-content-right"><img src="@/assets/img/cloud.svg" alt="alt"><div class="main-content-right-item"><img src="@/assets/img/icon-1.svg" alt="alt"><span>Температура</span><span>{{ Math.round(getFullWeather.current.temp) }}° - ощущается как {{ Math.round(getFullWeather.current.feels_like)}}°</span></div><div class="main-content-right-item"><img src="@/assets/img/icon-2.svg" alt="alt"><span>Давление</span><span v-if="getPressure == 760">{{getPressure}}мм ртутного столба - нормальное</span><span v-else-if="getPressure &lt; 760">{{getPressure}}мм ртутного столба - пониженное</span><span v-else-if="getPressure &gt; 760">{{getPressure}}мм ртутного столба - повышенное</span></div><div class="main-content-right-item"><img src="@/assets/img/icon-3.svg" alt="alt"><span>Влажность</span><span v-if="getHumidity &lt;= 40">{{getHumidity}}% - пониженная влажность</span><span v-else-if="getHumidity &gt; 40 &amp;&amp;  getHumidity &lt;= 60">{{getHumidity}}% - нормальная влажность</span><span v-else-if="getHumidity &gt; 60 &amp;&amp; getHumidity &lt;= 100">{{getHumidity}}% - повышенная влажность</span></div><div class="main-content-right-item"><img src="@/assets/img/icon-4.svg" alt="alt"><span>Ветер</span><span v-if="getWindDeg &lt;= 90">{{getFullWeather.current.wind_speed}} м/с северо-восток - {{windPower}} ветер</span><span v-else-if="getWindDeg &gt; 90 &amp;&amp;  getWindDeg &lt;= 180">{{getWindSpeed}} м/с юго-восток - {{windPower}} ветер</span><span v-else-if="getWindDeg &gt; 180 &amp;&amp;  getWindDeg &lt;= 270">{{getWindSpeed}} м/с юго-запад - {{windPower}} ветер</span><span v-else-if="getWindDeg &gt; 270 &amp;&amp;  getWindDeg &lt;= 360">{{getWindSpeed}} м/с северо-запад - {{windPower}} ветер</span></div></div></div><div class="loading" v-else><div class="loading__element"></div></div>