<div class="main-week-day"><h3>{{getWeekday}}</h3><h4>{{getDay}} {{getMonth}}</h4><img src="@/assets/img/sun.svg" alt="alt" v-if="main == 'Clear'"><img src="@/assets/img/mainly_cloudy.svg" alt="alt" v-else-if="main == 'Clouds'"><img src="@/assets/img/rain.svg" alt="alt" v-else-if="main == 'Rain'"><img src="@/assets/img/snow.svg" alt="alt" v-else-if="main == 'Snow'"><h5>{{Math.round(day.temp.max)}}°</h5><h6>{{Math.round(day.temp.min)}}°</h6><p>{{day.weather[0].description}}</p></div>